@import url('https://fonts.googleapis.com/css2?family=Andika&family=Mukta:wght@200&family=Signika+Negative:wght@300&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Andika&family=Mukta:wght@200&family=Poppins:wght@400;500&family=Signika+Negative:wght@300&display=swap');

*{
    box-sizing: border-box;
    font-family: 'Signika Negative', sans-serif;
}
.container{
    margin: auto;
    width: 700px;
    height: 700px;
    background-color: #282D35;
    
    
}

body{
    background-color: #34495e;
    
    
}



.nav{
    display: flex;
    align-items: center;
    background-color: #21222A;
    height: 90px;
    padding: 30px 25px;
    border-bottom: solid rgb(123, 123, 123) 1px;
    
    
    
   
}

.nav-img{
    
    height: 30px;
    margin-right: 7px;
}

.nav-title{
    margin: 0px;
    margin-right: auto;
    font-weight: 700;
}

.nav-text{
    margin: 0px;
    color: white;
    font-weight: 600;
    letter-spacing: 1px;

}

.main{
    background-color: #282D35;
    background-image: url(./images/react-img.png);
    background-size: 350px;
    background-repeat: no-repeat;
    background-position: 125% 90%;
    padding: 57px 27px;
    color: white;
    
}

.main-title{
    margin: 0px;
    font-size: 39px;
    letter-spacing: -0.05em;
}

.main-list{
    margin-top: 46px;
    font-size: 20px
    

}

.main-list > li{
    line-height: 19px;
    padding-block: 10px;
    max-width: 400px;

}

.main-list > li::marker{
    color: #61DAFB;
    font-size: 30px;
}


.material-symbols-outlined {
  font-variation-settings:
  'FILL' 0,
  'wght' 400,
  'GRAD' 0,
  'opsz' 48
}



.toggle{
    display: inline-flex;
    
}

span{
    margin: 2px;
    font-family: 'Poppins', sans-serif;
    letter-spacing: 1px;
    user-select: none;
}

button{
    width: 100px;
    height: 20px;
}